
.top{
    height: 50px;
    background-color: #f6f5f6;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
    > p{
        font-size: 20px;
        font-weight: 700;
    }
}
.info{
    height: calc(100vh - 50px);
    overflow-y: auto;
    span:first-child{
        // display: none !important;
        width: 24px !important;
        height: 24px !important;
        border-radius: 50% !important;
        background-color: #b6f0d7 !important;
        transform: translateX(-5px) translateY(-12.8px) !important;
    }
    > div{
        > div:first-child{
            padding: 20px;
            padding-top: 0;
            > div{
                margin-bottom: 60px;
                > p{
                    margin-bottom: 5px;
                    font-size: 24px;
                    font-weight: bold;
                    color: #c8c8c8;
                }
                > div{
                    width: 100vw;
                    transform: translateX(-20px);
                    padding: 20px;
                    font-size: 18px;
                    padding-bottom: 14px;
                    border-bottom: 1px solid rgb(218, 218, 218);
                }
            }
        }
        > div:last-child{
            text-align: center;
            > div{
                *{
                    padding-left: 0 !important;
                    padding-bottom: 1px !important;
                    div.ant-anchor-link-active > *{
                        color: black !important;
                    }
                    div{
                        text-align: center;
                        height: 24px !important;
                        margin-bottom: calc((100vh - 50px - 28.5px * 24) / (26 + 2)) !important;
                        line-height: 24px !important;
                    }
                }
                > div::before{
                    content: unset !important;
                }
            }
        }
    }
}